.body{
    background-color: rgb(0, 0, 0);
    height: 50px;
    color: white;
    position: fixed;
    top: 0px;
    width: 100vw;
    z-index: 3;
}

.body button{
    background-color: transparent;
    border: none;
    margin: 0px 20px 0px 20px;
    color: white;
}

.body button:hover{
    cursor: pointer;
    color: gray;
}