.base{
    background-image: url("img/mount1.jpg");
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
}

.body{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.body button{
    width: fit-content;
    border: none;
    padding: 20px;
    border-radius: 10px;
    margin: 30px;
}

.item{
    background-color: rgba(255, 255, 255, 0.719);
    color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    margin: 20px;
    border-radius: 30px;
}

.item a{
    color: black;
    font-weight: 500;
    text-decoration: none;
}

.item img{
    width: 50px;
    background-color: rgb(255, 251, 0);
    padding: 20px;
    border-radius: 20px;
}